<template>
  <div class="wrap"><!-- wrap--------- -->
    <header class="hd">
      <div class="hdwrap">
        <h1 @click="$router.push('/')">
          <!-- 로고이미지 -->
          <img src="https://cdn.imweb.me/thumbnail/20220530/acce454b13381.png" alt="logoimg">
        </h1>
        <nav>
          <div class="person">
            <ul>
              <li>LOGIN</li>
              <li>JOIN</li>
              <li>MY PAGE</li>
              <li>CART</li>
              <li class="gnbBtn" @click="menumtoggle">
                <span v-for="a in 3" :key="a"></span>
              </li>
            </ul>
          </div>
        </nav>
      </div>
      <div class="gnbModal" v-if="gnbbtn == 1" @click.self="menumtoggle">
      <!-- <div class="gnbModal" v-if="true"> -->
        <div class="gnbmodalinner" :style="`transform: translateX(${menumove}%);`">
          <div>
            <div class="gnbLogo">
              <img src="https://cdn.imweb.me/thumbnail/20220530/ad60405c42dad.png" alt="logo">
            </div>
            <ul>
              <li v-for="(a,i) in gnbMenu" :key="i" :data-menu="gnbMenu[i].pra" @click="menuclick">{{gnbMenu[i].menu}}</li>
            </ul>
          </div>
        </div>
      </div>
    </header>
    <router-view></router-view>
    <footer>
      <div class="footerWrap">
        <div>
          <span>이용약관</span>
          <span>개인정보처리방침</span>
        </div>
      </div>
    </footer>
  </div><!----------- wrap -->
</template>

<script>

export default {
  name: 'App',
  data() {
    return {
      gnbbtn : 0,
      menumove :-100,
      gnbMenu:[
        {menu: 'ABOUT', pra:'about'},
        {menu: 'SHOP', pra:'shop'},
        {menu: 'VIDEO', pra:'video'},
        {menu: 'NOTICE', pra:'notice'},
        {menu: 'REVIEW', pra:'review'},
        {menu: 'Q&A', pra:'qna'},
      ]
    }
  },
  methods: {
    menumtoggle(){
      setTimeout(() => {
        if(this.menumove == 0){
        this.menumove=-100;
      }else{this.menumove=0;}
      }, 10);


      if(this.gnbbtn == 0){
        this.gnbbtn = 1;
      }else{
        if(this.menumove == 0){
          setTimeout(() => {
            this.gnbbtn = 0;
          }, 300);
      }
      }
    },


    menuclick(e){
      let click = e.target.dataset.menu
      console.log(click)
      this.$router.push(`/${click}`)
      setTimeout(() => {
        if(this.menumove == 0){
        this.menumove=-100;
      }else{this.menumove=0;}
      }, 10);


      if(this.gnbbtn == 0){
        this.gnbbtn = 1;
      }else{
        if(this.menumove == 0){
          setTimeout(() => {
            this.gnbbtn = 0;
          }, 300);
      }
      }
    }


  },
  components: {

  },
  updated() {

  },
}
</script>

